
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Masker from 'vux/src/components/masker/index.vue'

import {format, subDays} from 'date-fns';

import ChangePaths from './ChangePaths.vue';

export default {
directives: {
TransferDom
},
components: {
XHeader,
Group,
Cell,
XButton,
Flexbox,
FlexboxItem,
Divider,
Calendar,
Badge,
Popup,
Countup,
FormPreview,
Selector,
Masker,
ChangePaths
},
created: function () {
if (this.$ISJS.existy(this.$route.params.authObj)) {
this.authObj = this.$route.params.authObj;
} else {
this.authObj = {
id: 0,
auth: "",
mark: ""
}
}


this.startDate = this.aWeekAgoFEnd;
},
mounted: function () {
this.getProjectList();
},
computed: {

aWeekAgoFEnd: function () {
var tmpdate = format(this.endDate, 'YYYY-MM-DD');
var weekAgoDay = subDays(tmpdate, 7);
var weekAgoDayStr = format(weekAgoDay, 'YYYY-MM-DD');

return weekAgoDayStr;
},
allCodeLines: function () {

var lines = 0;
for (var index in this.commitLogList) {
lines += (this.commitLogList[index].code_lines);
}
return lines;
},
isShowItemPopup:function () {

if(this.$ISJS.not.empty(this.clickedItemDetail)
&& this.clickedItemDetail.id > 0){

return true;
}else {
return false;
}
}
},
methods: {
getCommitLogs: function () {


if (this.$ISJS.not.existy(this.authObj.id)
|| this.$ISJS.empty(this.authObj.id)
|| this.authObj.id < 1) {
this.showConfirm(" ,", () => {
this.$router.push("/");
});

return;
}









if (this.selectedProjectId <= 0){
this.showAlert("");
return;
}


this.isLoadingHttp = true;
this.BaseHttp("./code/commitLogs", {
startDate: this.startDate,
endDate: this.endDate,
auth: this.authObj.auth,
projectId: this.selectedProjectId
}, (response) => {
this.isLoadingHttp = false;
if(this.$ISJS.not.empty(response)){
this.commitLogList = response;
}else {
this.showAlert('');
}
}, (errorMsg) => {
this.isLoadingHttp = false;
this.showAlert(errorMsg);
});

},
getFormListData: function (itemObj) {
var retList = [];

retList.push({
label: '',
value: itemObj.commit_time
});

retList.push({
label: '',
value: itemObj.commit_log
});

return retList;

},
onItemClicked: function (item) {
this.clickedItemDetail = item;
},
onDetailPopupHidden:function () {
this.clickedItemDetail = {};
},
getProjectList: function () {
this.BaseHttp("./project/all", {
searchText: ''
}, (response) => {
this.projectList = response;
}, (errorMsg) => {
this.showAlert('');
});
}
},
data() {
return {
isLoadingHttp: false,
authObj: {},
startDate: "",
endDate: format(new Date(), 'YYYY-MM-DD'), // TODAY
commitLogList: [],
clickedItemDetail: {},
projectList: [],
selectedProjectId:null
}
}
}



