
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'



export default {
directives: {
TransferDom
},
components: {
Group,
Cell,
XTextarea
},
props: {
logbean: {
type: Object,
default: {}
}
},
watch: {
logbean: function (bean) {
if (this.$ISJS.empty(this.logbean)
|| this.$ISJS.not.existy(this.logbean.id)) {

return;
}
this.getChangePaths();
}
},
methods: {
getChangePaths: function () {
if (this.$ISJS.empty(this.logbean)
|| this.$ISJS.not.existy(this.logbean.id)) {
this.showAlert(" logbean prop");
return;
}
this.BaseHttp("./changepaths/query", {
revisionId: this.logbean.revision_id
}, (response) => {
this.changePathList = response;
}, (errorMsg) => {
this.showAlert(errorMsg);
});
},
refreshChangePaths: function () {
if (this.$ISJS.empty(this.logbean)
|| this.$ISJS.not.existy(this.logbean.id)) {
this.showAlert(" logbean prop");
return;
}
this.showConfirm("",
()=> {
this.BaseHttp("./changepaths/refresh", {
revisionId: this.logbean.revision_id
}, (response) => {
this.showAlert("");
this.getChangePaths();
}, (errorMsg) => {
this.showAlert(errorMsg);
});
})



}
},
data() {
return {
commitLogObj: {},
changePathList: []
}
}
}



